

.main {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.right {
  background-image: url(https://cdn2.hubspot.net/hubfs/6994178/Pictures/Outdoor%20Architecture/UM6P_Campus_Ben-Guerir_Outdoor_Architecture_18.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  height: 50%;
}

.left {
  background-color: #ee6d69;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 45px;
  box-sizing: border-box;
  height: 50%;
}

.list {
  list-style-type: none;
  display: flex;
  width: 40%;
  justify-content: space-between;
  margin-left: 50px;
}

.h1 {
  font-size: 55px;
}

.item {
  border-top: 2px solid transparent;
  padding:  20px 0;
  cursor: pointer;
  width: fit-content;
  font-size: 18px;
}

.item:hover {
  border-top: 2px solid #ee6d69;
  color: #ee6d69;
}

.image {
  border-radius: 50%;
  width: 300px;
}

.div-of-image {
  display: grid;
  place-content: center;
  height: 80%;
}

.btn {
  display: flex;
  justify-content: end;
}
.btn {
  display: none;
}

.btn button {
  border: 2px solid #ffffff;
  padding: 0 50px;
  height: 50px;
  background-color: transparent;
  color: #ffffff;
  font-weight: 600;
  border-top-right-radius: 35px;
  border-bottom-left-radius: 35px;
  border-top-left-radius: 5px;
  border-bottom-right-radius: 5px;
}

.content {
  color: #ffffff;
}

.p {
  font-size: 30px;
  width: 400px;
}

.btn-more {
  border: 2px solid #ffffff;
  color: #ee6d69;
  border-top-right-radius: 35px;
  border-bottom-left-radius: 35px;
  padding: 0 60px;
  height: 60px;
  font-size: 20px;
  margin-top: 30px;
}

@media only screen and (min-width: 1000px) {
  .main {
    display: flex;
  }

  .right {
    width: 60%;
    height: auto;
  }

  .left{
    width: 40%;
    height: auto;
  }

  .btn {
    display: flex;
  }
}